.winnerCard{
    width: 330px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.808);
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
}

#trophy{
    font-size: 60px;
    color: rgb(102, 102, 102);

}
#flag1{
    transform: scaleX(-1) rotate(20deg);
    font-size: 25px;
    color: rgb(131, 131, 131);
}
#flag2{
    transform: rotate(20deg);
    font-size: 25px;
    color: rgb(131, 131, 131);
}