.startGame{
    width: 340px;
    height: 416px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    background: rgba(255, 255, 255, 0.808);
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    order: 1;
}

#submitBtn{
    display: block;
    background-color: rgb(101, 201, 174);
    color: white;
    border: none;
    border-radius: 6px;
    height: 40px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    cursor: pointer;
    letter-spacing: 2px;
}

#submitBtn:active{
    transform: scale(0.98);
}
#submitBtn:focus{
    outline: none;
}
#submitBtn:hover{
    background-color: rgb(87, 179, 154);
}

#username{
    display: block;
    border: none;
    border-radius: 6px;
    height: 40px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    padding: 0 10px;
    letter-spacing: 2px;
}

