.gameOverCard{
    width: 330px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.808);
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
}

#btnSubmit{
    display: block;
    background-color: rgb(101, 201, 174);
    color: white;
    border: none;
    border-radius: 6px;
    height: 40px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    cursor: pointer;
    letter-spacing: 2px;
}

#btnSubmit:active{
    transform: scale(0.98);
}
#btnSubmit:focus{
    outline: none;
}
#btnSubmit:hover{
    background-color: rgb(87, 179, 154);
}

.gameOverCard h3{
    text-align: center;
}

.progress{
    margin: 20px 0;
}

.progress p{
    margin-top: 5px;
}

.progress i{
    font-size: 30px;
    color: gray;
}