.leaderBoard{
    width: 340px;
    height: 416px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    background: rgba(255, 255, 255, 0.808);
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    order:2;
}
.leaderBoard i{
    font-size: 50px;
    margin-top: 15px;
    color: rgb(90, 90, 90);
 
}

.leaderBoard h1{
    margin-bottom: 15px;
}


.score{
    display: grid;
    grid-template-columns: 0.5fr 1fr 1fr 1fr;
    text-align: center; 
    background-color: rgba(129, 129, 129, 0.219);
    border-radius: 10px;
    padding: 10px;
    
}

.score h3{
    margin-bottom: 5px;
}
.score p{
    margin-bottom: 3px;
}

#first{
    font-size: 15px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 237, 78);
}

#second{
    font-size: 15px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(250, 250, 250);
}

#third{
    font-size: 15px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 201, 158);
}