#flagImg{
    max-width: 300px;
    height: auto;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
}

.main{
    width: 330px;
    height: 416px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    background: rgba(255, 255, 255, 0.808);
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.answers{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.answerBtn{
    margin: 5px 0;
    height: 40px;
    background-color: white;
    border: none;
    border-radius: 26px;
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    cursor: pointer;
    letter-spacing: 2px;
}

.answerBtn:hover{
    background-color: rgb(241, 241, 241);
}
.answerBtn:focus{
    outline: none;
}

.answerBtn:active{
    transform: scale(0.99);
}

.answerBtn.correct{
    background-color: rgb(86, 148, 86);
}
.answerBtn.wrong{
    background-color: rgb(199, 95, 95);
}

.topPanel{
    width: 330px;
    height: 50px;
    background: rgba(255, 255, 255, 0.808);
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
    margin-top: 20px;
    -webkit-box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    -moz-box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    border-radius: 10px;
    color: rgb(75, 75, 75);
}

.topPanel span i{
    font-size: 20px;
    color: rgb(85, 85, 85);
    margin: 10px;
}

