@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
.leaderBoard{
    width: 340px;
    height: 416px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    background: rgba(255, 255, 255, 0.808);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    order:2;
}
.leaderBoard i{
    font-size: 50px;
    margin-top: 15px;
    color: rgb(90, 90, 90);
 
}

.leaderBoard h1{
    margin-bottom: 15px;
}


.score{
    display: grid;
    grid-template-columns: 0.5fr 1fr 1fr 1fr;
    text-align: center; 
    background-color: rgba(129, 129, 129, 0.219);
    border-radius: 10px;
    padding: 10px;
    
}

.score h3{
    margin-bottom: 5px;
}
.score p{
    margin-bottom: 3px;
}

#first{
    font-size: 15px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 237, 78);
}

#second{
    font-size: 15px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(250, 250, 250);
}

#third{
    font-size: 15px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 201, 158);
}
.startGame{
    width: 340px;
    height: 416px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    background: rgba(255, 255, 255, 0.808);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    order: 1;
}

#submitBtn{
    display: block;
    background-color: rgb(101, 201, 174);
    color: white;
    border: none;
    border-radius: 6px;
    height: 40px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    cursor: pointer;
    letter-spacing: 2px;
}

#submitBtn:active{
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}
#submitBtn:focus{
    outline: none;
}
#submitBtn:hover{
    background-color: rgb(87, 179, 154);
}

#username{
    display: block;
    border: none;
    border-radius: 6px;
    height: 40px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    padding: 0 10px;
    letter-spacing: 2px;
}


.leaderAndStart{
    display: flex;
    margin: 0 auto;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 30px;
}
#flagImg{
    max-width: 300px;
    height: auto;
    border-radius: 10px;
    box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
}

.main{
    width: 330px;
    height: 416px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    text-align: center;
    background: rgba(255, 255, 255, 0.808);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.answers{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.answerBtn{
    margin: 5px 0;
    height: 40px;
    background-color: white;
    border: none;
    border-radius: 26px;
    box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    cursor: pointer;
    letter-spacing: 2px;
}

.answerBtn:hover{
    background-color: rgb(241, 241, 241);
}
.answerBtn:focus{
    outline: none;
}

.answerBtn:active{
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
}

.answerBtn.correct{
    background-color: rgb(86, 148, 86);
}
.answerBtn.wrong{
    background-color: rgb(199, 95, 95);
}

.topPanel{
    width: 330px;
    height: 50px;
    background: rgba(255, 255, 255, 0.808);
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
    margin-top: 20px;
    box-shadow: 0px 0px 53px -9px rgba(0, 0, 0, 0.473);
    border-radius: 10px;
    color: rgb(75, 75, 75);
}

.topPanel span i{
    font-size: 20px;
    color: rgb(85, 85, 85);
    margin: 10px;
}


.winnerCard{
    width: 330px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.808);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
}

#trophy{
    font-size: 60px;
    color: rgb(102, 102, 102);

}
#flag1{
    -webkit-transform: scaleX(-1) rotate(20deg);
            transform: scaleX(-1) rotate(20deg);
    font-size: 25px;
    color: rgb(131, 131, 131);
}
#flag2{
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    font-size: 25px;
    color: rgb(131, 131, 131);
}
.gameOverCard{
    width: 330px;
    padding: 15px 15px;
    background-color: white;
    margin: 10px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.808);
    box-shadow: 0px 0px 53px -9px rgba(0,0,0,0.85);
    letter-spacing: 1px;
    color: rgb(88, 88, 88);
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
}

#btnSubmit{
    display: block;
    background-color: rgb(101, 201, 174);
    color: white;
    border: none;
    border-radius: 6px;
    height: 40px;
    width: 80%;
    margin: 0 auto;
    margin-top: 10px;
    cursor: pointer;
    letter-spacing: 2px;
}

#btnSubmit:active{
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}
#btnSubmit:focus{
    outline: none;
}
#btnSubmit:hover{
    background-color: rgb(87, 179, 154);
}

.gameOverCard h3{
    text-align: center;
}

.progress{
    margin: 20px 0;
}

.progress p{
    margin-top: 5px;
}

.progress i{
    font-size: 30px;
    color: gray;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}
body{
    background: rgb(54,59,129);
    background: linear-gradient(90deg, rgba(54,59,129,1) 0%, rgba(71,114,184,1) 22%, rgba(82,150,220,1) 33%, rgba(88,170,240,1) 40%, rgba(91,179,249,1) 45%, rgba(93,185,255,1) 49%, rgba(92,181,251,1) 54%, rgba(90,175,245,1) 58%, rgba(85,157,227,1) 63%, rgba(73,119,189,1) 76%, rgba(54,59,129,1) 100%);
}
